import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Battle from '@/components/Battle'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Battle',
      component: Battle
    }
  ]
})



